body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #FFFFFF;
}

.App {
  text-align: center;
}

.controls {
  margin: 20px auto 40px auto;
  width: 100%;
}
.Tile {
  height: 100%;
  box-sizing: border-box;
  float: left;
  border: 5px solid #212121; }

.TileFlasher {
  height: 100%; }
  .TileFlasher.playing {
    -webkit-animation-name: tileFlash;
            animation-name: tileFlash;
    -webkit-animation-duration: 1s;
            animation-duration: 1s; }

@-webkit-keyframes tileFlash {
  0% {
    background-color: rgba(255, 255, 255, 0.5); }
  100% {
    background-color: rgba(255, 255, 255, 0); } }

@keyframes tileFlash {
  0% {
    background-color: rgba(255, 255, 255, 0.5); }
  100% {
    background-color: rgba(255, 255, 255, 0); } }

.Row {
  overflow: hidden;
  margin: 0 auto;
  -webkit-flex: 1 1;
          flex: 1 1;
  background: #212121;
  border-left: 5px solid #212121;
  border-right: 5px solid #212121; }

.RowOuter {
  display: -webkit-flex;
  display: flex;
  width: 100%; }

.longButtonOuter {
  width: 100%; }

.rowButton {
  opacity: 0;
  width: 40px;
  height: 100%;
  background: #212121;
  border-radius: 10px;
  color: white;
  font-family: Helvetica;
  font-weight: 500px;
  font-size: 20px;
  line-height: 200px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: white; }
  .rowButton.add {
    border-radius: 0px 10px 10px 0px; }
    .rowButton.add.long {
      border-radius: 0px 0px 10px 10px; }
  .rowButton.subtract {
    border-radius: 10px 0px 0px 10px; }
    .rowButton.subtract.long {
      border-radius: 10px 10px 0px 0px; }
  .rowButton.long {
    width: auto;
    width: initial;
    margin: 0 40px 0 40px;
    display: block;
    height: 40px;
    line-height: 40px; }
  @media (max-width: 600px) {
    .rowButton {
      font-size: 15px;
      width: 20px; }
      .rowButton.long {
        height: 20px;
        line-height: 20px;
        margin: 0 20px 0 20px; } }
  @media (min-width: 601px) {
    .rowButton:hover {
      opacity: 1;
      cursor: pointer; } }

.BoardInner {
  position: relative; }

.Board {
  margin: 0 auto;
  width: 100%;
  max-width: 800px; }

